import React from 'react'
import Page from '../layouts/main'

import "../sass/main.scss"

function Home({ name }) {
    return (
        <Page>
            <div>
                <p>Hi I'm {name}, a Dublin-based software programmer.</p>
                <p>I'm glad you've stumbled upon my website. I hope you'll stay for a while and look at some of my game projects and 3D artwork.</p>
                <p>Most of my website is still <b>in progress</b>. Check back soon and I might have more content.</p>
            </div>
        </Page>
    )
}

Home.getInitialProps = async ({ req }) => {
    var config = require('../assets/config.json');
    return { name: config.mynameis }
}

export default Home